<template>
  <div class="dashboard-container">
    <div class="app-container">
      <el-card shadow="never">
        <!-- 表单部分####################################################################################################### -->
        <el-form :model="form">
          <el-form-item label="关键字" label-width="80px">
            <el-input v-model="form.id" placeholder="根据编号搜索"></el-input>
            <div class="btn">
              <el-button @click="reset">清除</el-button>
              <el-button type="primary" @click="search">搜索</el-button>
            </div>
          </el-form-item>
        </el-form>

        <!-- 提示文本############################################################################################################## -->
        <div
          role="alert"
          class="el-alert alert el-alert--info is-light"
          style="margin-bottom: 15px"
        >
          <i class="el-alert__icon el-icon-info"></i>
          <div class="el-alert__content">
            <span class="el-alert__title">数据一共 {{ total }} 条</span
            ><!----><!----><i
              class="el-alert__closebtn el-icon-close"
              style="display: none"
            ></i>
          </div>
        </div>

        <!-- table表单############################################################################################################# -->
        <el-table :data="tableData" style="width: 100%">
          <el-table-column label="编号" prop="id" width="220"></el-table-column>
          <el-table-column
            label="题型"
            prop="questionType"
            width="80"
          ></el-table-column>
          <el-table-column label="题目编号" prop="questionIDs" width="300">
            <template #default="{ row }">
              <div class="cnm">
                <a
                  @click.prevent="opdetail(item)"
                  v-for="(item, index) in row.questionIDs"
                  :key="index"
                  >{{ item.number }}</a
                >
              </div>
            </template>
          </el-table-column>
          <el-table-column
            label="录入时间"
            prop="addTime"
            width="200"
          ></el-table-column>
          <el-table-column
            label="答题时间(s)"
            prop="totalSeconds"
          ></el-table-column>
          <el-table-column
            label="正确率(%)"
            prop="accuracyRate"
          ></el-table-column>
          <el-table-column label="录入人" prop="userName"></el-table-column>
          <el-table-column label="操作" width="80">
            <template #default="{ row }">
              <button
                type="button"
                class="el-button el-button--danger el-button--small is-plain is-circle"
              >
                <i class="el-icon-delete" @click="open(row)"></i>
              </button>
            </template>
          </el-table-column>
        </el-table>

        <!-- 分页############################################################################################################## -->
        <el-pagination
          background
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="page"
          :page-sizes="[10, 12, 15, 20]"
          :page-size="pagesize"
          layout="  prev, pager, next,sizes ,jumper"
          :total="total"
          hide-on-single-page
        >
        </el-pagination>

        <!-- 回显#################################################################################################################### -->
       <Container ref="questionPreview" :data="detailList"></Container>
      </el-card>
    </div>
  </div>
</template>

<script>
import { randoms, removeRandoms } from '@/api/hmmm/questions'
export default {
  async created () {
    this.initData()
  },
  data () {
    return {
      dialogVisible: false,
      page: 1,
      pagesize: 10,
      total: 0,
      tableData: [],
      list: [],
      detailList: {},
      flag: true,
      questionIDs: '',
      form: {
        id: ''
      }
    }
  },
  methods: {
    // 预览x关闭
    closeDig () {
      const v1 = document.querySelector('.video')
      v1.style.display = 'none'
      this.dialogVisible = false
    },
    // 视频预览
    openVideo () {
      const v1 = document.querySelector('.video')
      v1.style.display = 'block'
    },
    // 题目编号点击
    async opdetail (row) {
      this.detailList = row
      this.$nextTick(() => {
        this.$refs.questionPreview.open()
      })
    },
    handleClose (done) {
      this.$confirm('确认关闭？')
        .then((_) => {
          this.closeDig()
          done()
        })
        .catch((_) => {})
    },
    // 搜索
    search () {
      this.page = 1
      this.initData()
    },
    // 清除
    reset () {
      this.form.id = ''
    },
    open (id) {
      this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          this.del(id)
          this.$message({
            type: 'success',
            message: '删除成功!'
          })
        })
        .catch(() => {})
    },
    // 分页
    handleSizeChange (val) {
      this.pagesize = val
      this.page = 1
      this.initData()
    },
    handleCurrentChange (val) {
      this.page = val
      this.initData()
    },
    async del (id) {
      // 组题列表删除
      await removeRandoms(id)
      if (this.tableData.length === 1 && this.page > 1) {
        this.page--
      }
      this.initData()
    },
    // 搜索
    async initData () {
      const { data } = await randoms({
        page: this.page,
        pagesize: this.pagesize,
        keyword: this.form.id
      })
      //
      console.log(data)
      this.tableData = data.items
      this.total = data.counts
      this.tableData.map((val) => {
        if (val.questionType === '1') {
          val.questionType = '单选'
        } else if (val.questionType === '2') {
          val.questionType = '多选'
        } else {
          val.questionType = '简答'
        }
      })
    }
  }
}
</script>

<style scoped lang='scss'>
video{
  width: 400px;
  height: 300px;
}
.el-input {
  width: 282px;
  background-image: none;
  color: #606266;
  ::v-deep .el-input__inner {
    height: 32px;
    line-height: 32px;
  }
}
.btn {
  float: right;
}
.cnm {
  color: rgb(0, 153, 255);
  a {
    display: block;
  }
}
.el-col {
  padding: 10px 0;
}
.el-col-6 {
  width: 25%;
}
.el-dialog__footer {
  .el-button {
    position: absolute;
    right: 15px;
    bottom: 20px;
  }
}
.el-pagination {
  margin-top: 20px;
  text-align: right;
}
</style>
